*{
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}
body {
  padding: 2rem;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

input{
  color: grey;
  font-size: 1rem;
  width: 100%;
}

.card-grid{
display: flex;
flex-direction: row;
flex-wrap: wrap
;
}
.card{
align-content: center;
align-items: center;
border: 1px solid gray;
border-radius: 6px;
margin-bottom: 10px;
margin-right: 10px;
overflow: hidden;
}

.card p{
padding: 5px;
text-align: center;
}

.card img{
max-height: 170px;
width: auto;
}